/* src/styles.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
}

header, main, footer {
  margin: 0 auto;
  padding: 20px;
  max-width: 800px;
}

header {
  background-color: #3498db;
  color: white;
  text-align: center;
}

main {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

th, td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

footer {
  text-align: center;
  background-color: #3498db;
  color: white;
}
